import React from 'react';
import { FieldTimeOutlined, FolderOutlined, HomeOutlined, MenuFoldOutlined, SnippetsOutlined } from '@ant-design/icons';
import { Button, Space, Tabs } from 'antd';
import { ReactComponent as UrbiLogo } from '../../icons/urbi.svg';
import { useAppContext } from '../../AppContext';
import StaticLayersPanel from '../layersStatic';

const MainTabs: React.FC = () => {
    const { actions, setActions } = useAppContext();

    const Pressets = () => (<span><HomeOutlined />Pressets</span>);
    const Layers = () => (<span><FolderOutlined />Layers</span>);
    const hideSider = () => setActions({ ...actions, sideBar: true });

    const setDynamicLayers = () => setActions({ ...actions, layersDemo: 'dynamic' });
    const setStaticLayers = () => setActions({ ...actions, layersDemo: 'static' });

    const PressetsContent = () => <>
        <Space direction='vertical'>
            <Button className='multi-button' onClick={setDynamicLayers} ghost={actions.layersDemo === 'dynamic' ? false : true}>
                <div style={{ padding: 4, fontSize: '10px', color: 'grey' }}>
                    <div style={{ fontSize: '12px', color: 'black' }}><FieldTimeOutlined style={{ fontSize: '16px', color: 'rgb(74, 21, 232)' }} /> Dynamic styled layers</div>
                    <div>Creating ALD layer style in runtime</div>
                    <div>allow manual define style and layer order</div>
                </div>
            </Button>
            <Button className='multi-button' onClick={setStaticLayers} ghost={actions.layersDemo === 'static' ? false : true}>
                <div style={{ padding: 4, fontSize: '10px', color: 'grey' }}>
                    <div style={{ fontSize: '12px', color: 'black' }}><SnippetsOutlined style={{ fontSize: '16px', color: 'rgb(74, 21, 232)' }} /> In style layers</div>
                    <div>Static ALD Layers customizing and order</div>
                    <div>store layers in map style theme</div>
                </div>
            </Button>

        </Space>
    </>

    const layerPanel = actions.layersDemo === 'static' ? <StaticLayersPanel /> : <></>;

    return (

        <Tabs
            tabBarExtraContent={{ left: <UrbiLogo className='tabs-extra-logo' />, right: < Button className="tabs-extra-button" onClick={hideSider}><MenuFoldOutlined /></Button> }}
            items={
                [<Pressets />, <Layers />].map((Tab, i) => {
                    const id = String(i + 1);

                    return {
                        label: Tab,
                        key: id,
                        children: [<PressetsContent />, layerPanel][i],
                    };
                })
            }
        />)
};

export default MainTabs;