import { memo, useEffect } from 'react';
import { load } from '@2gis/mapgl';
import { useMapglContext } from './mapglContext';
import { useAppContext } from '../../AppContext';
import { dataSetsNames } from '../../core/appConfig';


const MapWrapper = memo(
    function MapContainer() {
        return <div id='map-container' style={{ width: '100%', height: '100%' }}></div>;
    },
    () => true,
);

export default function Mapgl() {
    const { setMapglContext } = useMapglContext();
    const { actions, setActions } = useAppContext();

    useEffect(() => {
        let map: mapgl.Map | undefined = undefined;

        load().then((mapgl) => {
            map = new mapgl.Map('map-container', {
                center: [54.55937, 24.43],
                zoom: 14,
                key: 'a1893935-6834-4445-b97a-3405fb426c5b',
                style: '34415e89-88f8-44bb-986e-c96cd0dcfd96',
                maxZoom: 22
            });

            setActions({ ...actions, dataSetsNames })

            setMapglContext({
                mapglInstance: map,
                mapgl,
            });
        });

        // Destroy the map, if Map component is going to be unmounted
        return () => {
            map && map.destroy();
            setMapglContext({ mapglInstance: undefined, mapgl: undefined });
        };
    }, [setMapglContext]);

    return <MapWrapper />;
}