import { ReactNode, createContext, useContext, useState } from "react"
export type AppContextActions = { dataSetsNames: string[], sideBar?: boolean, message?: { type: 'loading', content: string }, layersDemo?: 'dynamic' | 'static' };
export type AppContextState = {
    actions: AppContextActions
    setActions: (newActions: AppContextActions) => void
}
export const AppContext = createContext<AppContextState>({
    actions: { dataSetsNames: [], sideBar: false, layersDemo: 'static' },
    setActions: () => { },
})
export const useAppContext = () => useContext(AppContext)


export function AppContextProvider({ children }: { children: ReactNode }) {
    const [actions, setActions] = useState<AppContextActions>({ dataSetsNames: [], sideBar: false, layersDemo: 'static' });
    return (
        <AppContext.Provider value={{ actions, setActions }}>
            {children}
        </AppContext.Provider>
    );
}